import axios from 'axios'
import React, { useEffect, useState } from 'react'
import {   Container, Row, Table } from 'react-bootstrap'


const AllUserContacts = () => {
  const [userDetails, setUserDetails] = useState([])

  const getuserDetails = async () => {
    try {
      const res = await axios.get('/api/v1/masterAdmin/getUserDetails')
      if (res.data.success) {
        setUserDetails(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }
 
  useEffect(() => {
    getuserDetails()
  }, [])
  return (
    <>
      <Container fluid>
        <h3>Contact Details of users</h3>
          <Row>
            <Table responsive>
              <thead>
                <tr>
                  <th className="text-center">Sn</th>
                  <th className="text-center">Name</th>
                  <th className="text-center">UniqueId</th>
                  <th className="text-center">Mobile Number</th>
                  <th className="text-center">Pan No.</th>
                  <th className="text-center">State</th>
                  <th className="text-center">City</th>
                </tr>
              </thead>
              <tbody>
                {userDetails && userDetails.length > 0 ? (
                  userDetails.map((item, index) => (
                    <tr key={index}>
                      <td className="text-center">{index + 1}</td>
                      <td className="text-center">{item.name}</td>
                      <td className="text-center">{item.uniqueId}</td>
                      <td className="text-center">{item.mobileNumber}</td>
                      <td className="text-center">{item.panCard}</td>
                      <td className="text-center">{item.state}</td>
                      <td className="text-center">{item.city}</td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={8} className="text-center text-danger">
                      No data found
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>
          </Row>
      </Container>
    </>
  )
}

export default AllUserContacts